* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    background: #e5e5e5;
    color: #101010;
    -webkit-font-smoothing: antialiased;
}

body, input, button {
    font: 16px Lato, sans-serif;
    // padding: 0 20px;
}

#root {
    margin: 0 auto;
}

button {
    cursor: pointer;
}

a {
    text-decoration: none;
    color: #101010;
}
